body {
  font: 20px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

/*.board-row:after {
  clear: both;
  content: "";
  display: table;
}*/

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 4em;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game-board{
  display: flex;
  flex-direction: row;

}

.game-info {
  margin-top: 30px;
  margin-left: 30px;
}

@media only screen and (max-width: 540px){
  .square{
    font-size: 2em;
    height: 50px;
    width: 50px;
  }
  .game-info {
  margin-left: 10px;
  float: none;
  }
  body{
    margin: 3px;
  }
}

@media only screen and (min-width: 430px){
  .game{
    display: flex;
    flex-direction: row;
  }
}